.brands {
    &__filter {
        padding: 18px 30px 28px;
        border: 1px solid var(--Gainsboro);
        border: 1px solid #e1e1e1;
        &-inner {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
        }
        a {
            text-transform: uppercase;
            transition: all 0.2s;
            line-height: 33px;
            &:not(:last-child) {
                margin-right: 15px;
            }
            &:hover {
                color: var(--Flamenco);
                color: #e08b44;
            }
        }
        &-disable {
            color: var(--hrefDisable);
            color: #a3a2a2;
            pointer-events: none;
        }
        &-btn {
            margin-top: 12px;
            color: var(--Charcoal);
            line-height: 1.1;
            border-bottom: 1px dashed var(--Charcoal);
            border-bottom: 1px dashed #494949;
            transition: all 0.2s;
            &:hover {
                border-bottom: 1px dashed var(--White);
                border-bottom: 1px dashed #ffffff;
            }
        }
    }
    &__result {
        &-block {
            &:first-child {
                padding-bottom: 45px;
                border-bottom: 1px dashed var(--Gainsboro);
                border-bottom: 1px dashed #e1e1e1;
            }
            &:last-child {
                margin-top: 5px;
            }
        }
        &-item {
            margin-top: 40px;
            li:not(:last-child) {
                margin-bottom: 18px;
            }
            a {
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                line-height: 1.3;
                transition: all 0.2s;
                &:hover {
                    color: var(--Flamenco);
                    color: #e08b44;
                }
            }
        }
        &-title {
            font-size: 20px;
            color: var(--Wedgewood);
            color: #445f87;
            font-weight: 700;
            margin: 0;
            margin-bottom: 25px;
        }
    }
    .feedback {
        &__inner {
            padding: 23px 44px;
        }
    }
}
@media(max-width: 1700px) {
    .brands {
        .feedback {
            &__inner {
                padding: 23px 15px;
            }
        }
    }
}
@media(max-width: 925px) {
    .brands {
        &__filter {
            padding: 10px 20px 20px;
            a {
                &:not(:last-child) {
                    margin-right: 10.5px;
                }
            }
        }
    }
}

@media(max-width: 767px) {
    .brands {
        &__filter {
            padding: 10px 20px 20px;
            a {
                line-height: 33px;
            }
            .row {
                margin: 0;
            }
            &-btn {
                margin-top: 5px;
            }
            .col-auto {
                padding: 0;
            }
        }
        &__result{
            margin-top: -10px;
            &-title {
                margin-bottom: 18px;
            }
            &-block:first-child {
                padding-bottom: 30px;
            }
            &-block:last-child {
                margin-top: -10px;
            }
        }
        .feedback {
            margin: 40px 0 30px;
        }
    }
}
@media(max-width: 620px) {
    .brands {
        &__filter {
            &-block.col-auto {
                padding: 6px 0;
            }
            &-block {
                border-bottom: 1px dashed var(--Gainsboro);
                border-bottom: 1px dashed #e1e1e1;
                width: 100%;
            }
            &-btn {
                margin-top: 10px;
            }
        }
    }
}
@media(max-width: 576px) {
    .brands {
        &__filter {
            padding: 4px 15px 20px;
            a {
                &:not(:last-child) {
                    margin-right: 9px;
                }
            }
        }
        &__result {
            margin-top: 0;
            &-item {
                margin-top: 30px;
                li:not(:last-child) {
                    margin-bottom: 13px;
                }
                a {
                    font-size: 14px;
                }
            }
            &-title {
                font-size: 21px;
            }
        }
        &__result-block:last-child {
            margin-top: 0;
        }
        .feedback {
            margin: 30px 0;
        }
        .text-block .section-title  {
            margin-bottom: 0;
        }
    }
}